@use "base/mixins" as m;
@use "base/variables" as v;

.acciones {
    margin-top: 5rem;

    @include m.tablet { 
        display: flex;
        align-items: center;
        justify-content: space-between;
    } 

    a {
        display: block;
        color: v.$negro;
        text-align: center;
        margin-bottom: 2rem;
    }
}